









































































































































































































input[type=radio] {
  opacity: 0;
}
.progress-bar,
.list-stat {
  flex-basis: auto;
}
.btn {
  margin-top: 20px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  height: 36px;
  min-width: 125px;
  padding: 0 16px;
  border-radius: 3px;
  transition: background 0.2s cubic-bezier(0.4, 0, 0.6, 1);
  white-space: nowrap;
}
.reset-btn {
  background: white;
  &:hover {
    background: #fdf1f6;
  }
}
.complete-btn {
  background: white;
  &:hover {
    background: #ecedf6;
  }
}
.add-btn {
  background: #283593;
  margin-left: 20px;
  margin-top: 0;
  &:hover {
    background: #8289c0;
  }
}
.todo-item-wrapper,
.progress-bar {
  display: flex;
}
.progress-bar {
  flex-direction: column;
  align-items: center;
  .v-progress-circular {
    margin-top: 20px;
  }
}
.footer {
  &__filter-item {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    li {
      width: 200px;
      padding: 10px 0;
      cursor: pointer;
      border-bottom: 3px solid transparent;
      label {
        width: 200px;
        cursor: pointer;
        text-transform: uppercase;
        font-size: 14px;
      }
      &.checked {
        border-bottom: 3px solid #283593;
      }
    }
  }
}
