

















































































input[type=checkbox] {
    opacity: 0;
}
.item-wrapper {
    display: flex;
    flex-wrap: nowrap;
    padding: 10px 0;
    label {
        .v-icon {
            cursor: pointer;
        }
    }
    .theme--light.v-icon.color-blue {
        color: #283593;
    }
    .theme--light.v-icon.color-pink {
        color: #D81B60;
    }
}
.btn-group {
    display: flex;
    flex-wrap: nowrap;
    padding: 0 20px;
    .btn-icon {
        padding: 0 10px;
    }
}
