

































































input {
    display: flex;
    flex: 1 1 auto;
    position: relative;
}
label {
    left: 0px;
    right: auto;
    position: absolute;
    height: 20px;
    line-height: 20px;
    color: rgba(0, 0, 0, 0.54);
    max-width: 90%;
    overflow: hidden;
    text-overflow: ellipsis;
    top: 6px;
    transform-origin: top left;
    white-space: nowrap;
    pointer-events: none;
    font-size: 16px;
    min-height: 8px;
    transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
}
.todo-input {
    align-items: center;
    display: flex;
    margin-bottom: 8px;
    min-height: 32px;
    position: relative;
    transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
    width: 100%;
    cursor: text;
    &:before {
        bottom: -1px;
        content: "";
        left: 0;
        position: absolute;
        transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
        width: 100%;
        border-color: rgba(0, 0, 0, 0.42);
        border-style: solid;
        border-width: thin 0 0 0;
    }
    &::after {
        border-color: currentColor;
        border-style: solid;
        border-width: thin 0 thin 0;
        transform: scaleX(0);
        bottom: -1px;
        content: "";
        left: 0;
        position: absolute;
        transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
        width: 100%;
    }
}
.focused {
    &:after {
        transform: scaleX(1);
        border-color: #283593;
    }
    input:focus + label {
        transform: translateY(-18px) scale(0.75);
        color: #283593;
    }
}
.readOnly {
    &:before,
    &:after {
        content: none;
    }
}
